import { useNavigate } from "react-router-dom";
import { Button } from "antd";

export default function Scan() {
  const navigate = useNavigate();

  function handleClick() {
    navigate("/scan/1");
  }
  return (
    <>
      {/* <Questionnaire /> */}

      <Button type="primary" onClick={handleClick}> 开始 </Button>
    </>
  );
}
